import { MyLib } from "@macashipo/mlib"
import React from "react"
import { Popover } from "src/core/components"
import styled from "styled-components"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import { Button } from "reactstrap"
const WrapCell = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  & > div {
    border: 1px solid #f1f4f6;
    border-radius: 12px;
    height: 24px;
    width: 24px;
    cursor: pointer;
    box-shadow: inset 0 0 1px #ffffff;
  }
`
function MTCSelectModernPopover(props) {
  const { ctc } = props
  const [color, setColor] = React.useState(ctc.valueCell || "#999")
  return (
    <div style={{ minWidth: "100px", minHeight: "50px" }}>
      <MyLib
        type="chromepicker"
        color={color}
        style={{ boxShadow: "none" }}
        onChange={color => {
          // console.log("onchange color: ", color)
          setColor(color)
        }}
      />
      <Button
        color="primary"
        style={{ width: "100%" }}
        onClick={() => {
          let _hexColor = color.hex || color
          ctc.updateFieldData(_hexColor)
          if (props.hide) {
            props.hide()
          }
        }}
      >
        OK
      </Button>
    </div>
  )
}
export default function MTCChromeColor(props) {
  const { cell } = props
  const ctc = useConfigTableCell({ props })
  let _isNoBgColor = false
  let _bgColor = ""
  if (ctc.valueCell) {
    _bgColor = ctc.valueCell
  }
  if (_bgColor == null || _bgColor == "") {
    _isNoBgColor = true
  }
  let _styleDiv = { background: _bgColor }
  if (_isNoBgColor) {
    _styleDiv = {}
  }
  return (
    <WrapCell className={ctc.classNameWrap("mct-chromecolor")}>
      <Popover
        overlay={props => {
          return <MTCSelectModernPopover ctc={ctc} {...props} />
        }}
        overlayClassName="m-popover m-popover-full"
        placement="bottom"
        destroyTooltipOnHide={true}
        disabled={ctc.canEdit === false}
      >
        <div
          style={_styleDiv}
          className={_isNoBgColor ? "bg-no-color" : ""}
        ></div>
      </Popover>
    </WrapCell>
  )
}
